.hero-row {
    height: 40rem;
    background-image: url(../images/cover.jpg);
    background-position: center top;
    background-size: cover;
}

.hero-img {
    position: relative;
    width: 100%;
}

.hero-img img {
    width: 100%;
}

.hero-layer {
    background-color: white;
    box-shadow: 0px 15px 16px 0px rgba(0, 0, 0, 0.2);
    width: 45rem;
    height: 25rem;
    position: absolute;
    bottom: 13%;
    left: 4%;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: center;
    border-radius: 0px;
    color: #222E3E;
    padding: 15px 1.5rem;
    text-align: start;
}

.hero-layer h2 {
    font-size: 70px;
    text-align: start;
    color: #0a1441;
    font-weight: bold;
    font-family: "roboto-slab", sans-serif;
}

.hero-layer p {
    font-size: 16px;
}

.buttons .btn {
    background-color: #E32845;
    color: white;
    margin: 15px;
    position: relative;
    z-index: 1;
    border-radius: 5px;
    padding: 1rem 2.5rem !important;
}

.buttons .btn::before {
    content: "";
    position: absolute;
    background-color: white;
    width: 100%;
    height: 50%;
    z-index: -1;
    top: 0;
    left: 0;
    transform: scaleX(0);
    transition: 0.5s;
    transform-origin: left;
}

.buttons .btn::after {
    content: "";
    position: absolute;
    background-color: white;
    width: 100%;
    height: 50%;
    z-index: -1;
    bottom: 0;
    left: 0;
    transform: scaleX(0);
    transition: 0.5s;
    transform-origin: right;
}

.buttons .btn:hover::before,
.buttons .btn:hover::after {
    transform: scaleX(1);
}

.buttons .btn:hover {
    color: black;
}

.hero-cat-row {
    display: flex;
    position: relative;
    bottom: 5rem;
}

.hero-cat-col {
    flex: 1;
    text-align: center;
    height: 15rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    color: white;
    z-index: 2;
    line-height: 40px;
    color: white;
}

.hero-cat-col h4 {
    color: white;
}

.hero-cat-layer {
    background-color: rgb(16, 29, 95, 0.6);
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
}

.hero-cat-layer::before {
    content: "";
    position: absolute;
    background-color: #E32845;
    width: 100%;
    height: 100%;
    left: 0;
    transform: scaleX(0);
    transform-origin: left;
    transition: 0.5s;
}

.hero-cat-layer::after {
    content: "";
    position: absolute;
    background-color: #E32845;
    width: 100%;
    height: 100%;
    left: 0;
    transform: scaleX(0);
    transform-origin: left;
    transition: 0.5s;
}

.hero-cat-col:hover .hero-cat-layer::before,
.hero-cat-col:hover .hero-cat-layer::after {
    transform: scaleX(1);
}

.hero-cat-col .icon {
    font-size: 50px;
}

.hero-cat-col:nth-child(1) {
    background-image: url(../images//study.jpg);
    background-position: center;
    background-size: cover;
}

.hero-cat-col:nth-child(2) {
    background-image: url(../images//study2.jpg);
    background-position: center;
    background-size: cover;
}

.hero-cat-col:nth-child(3) {
    background-image: url(../images//study3.jpg);
    background-position: center;
    background-size: cover;
}

.hero-cat-col:nth-child(4) {
    background-image: url(../images//study4.jpg);
    background-position: center;
    background-size: cover;
}

.hero-cat-col a {
    text-decoration: none;
    color: white;
    transition: 0.5s;
}

.hero-cat-col a:hover {
    color: #0a1441;
}

/**********
Slider CSS
***********/
.mySwiperHero {
    height: 40rem !important;
}

.swiper {
    width: 100%;
    height: 100%;
}

.hero-slide {
    text-align: center;
    font-size: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
}


/**********
Slider CSS
***********/


.nav-buttons {
    height: 3rem;
    z-index: 10;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}

.nav-buttons i {
    font-size: 40px;
    color: white;
    cursor: pointer;
    z-index: 5;
    border-radius: 50%;
    border: 3px solid;
}

.nav-buttons i:hover {
    background-color: #E32845;
}

