.event-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mySwiperEvent {
    width: 84% !important;
}


.event-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.event-row {
    padding: 7rem 0rem;
}

.event-col {
    line-height: 28px;
    transition: 0.5s;
}

.event-col:hover {
    transform: translateX(-2%);
}

.event-content {
    padding: 10px 10px;
}

.event-col h3 {
    color: #101D5F;
    text-align: start;
    font-size: 20px;
    margin-top: 1.5rem;
    font-weight: bold;
    font-family: "roboto slab";
}

.event-col p {

    font-size: 16px;
    text-align: start;
}

.event-col a {
    color: #E32845;
    display: flex;
    align-items: center;
    text-decoration: none;
}

.event-footer {
    display: flex;
    justify-content: space-between;
    padding: 0rem 1rem;
    border-top: 1px solid rgba(0, 0, 0, 0.101);
    height: 4rem;
    align-items: center;
    font-size: 16px;
}

@media screen and (max-width: 600px) {
    .event-slide {
        text-align: center;
        font-size: 18px;
        background: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .mySwiperEvent {
        width: 100% !important;
    }


    .event-slide img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .event-row {
        padding: 7rem 0.5rem;
    }

    .event-col {
        line-height: 28px;
        transition: 0.5s;
    }

    .event-col:hover {
        transform: translateX(-2%);
    }

    .event-content {
        padding: 10px 10px;
    }

    .event-col h3 {
        color: #101D5F;
        text-align: start;
        font-size: 20px;
        margin-top: 1.5rem;
        font-weight: bold;
        font-family: "roboto slab";
    }

    .event-col p {

        font-size: 16px;
        text-align: start;
    }

    .event-col a {
        color: #E32845;
        display: flex;
        align-items: center;
        text-decoration: none;
    }

    .event-footer {
        display: flex;
        justify-content: space-between;
        padding: 0rem 1rem;
        border-top: 1px solid rgba(0, 0, 0, 0.101);
        height: 4rem;
        align-items: center;
        font-size: 16px;
    }
}