.courses-about h3,
h4 {
    color: #111D5E;
    font-family: 'roboto slab';
}

.courses-text li {
    list-style: none;
    margin: 0.5rem 0rem;
}

.courses-text li::before {
    content: "\2022";
    color: #E32845;
    font-size: 30px;
    margin-right: 1rem;
    position: relative;
    top: 0.2rem;
}

.courses-social i {
    
    background-color: #eee;
    margin: 0rem 0.5rem;
    padding: 15px;
    border-radius: 50px;
    cursor: pointer;
    color: #E32845;
    transition: allb 0.5s;
}

.courses-social i:hover {
    background-color: #E32845;
    color: #fff;
}