.care-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;

    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
}

.care-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.mySwiperCare {
    width: 80% !important;
}

.care-row {
    padding: 8rem 0rem;
    background-color: #eee;
}

.care-col {
    position: relative;
    cursor: pointer;
}

.care-col:hover p {
    color: #E32845;
}

.care-layer {
    background-color: white;
    position: absolute;
    width: 90%;
    height: 30%;
    top: 80%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0rem 1rem;
}

.care-layer a {
    text-decoration: none;
    color: #101D5F;
    font-size: 15px;
}

.care-layer a:hover {
    color: #E32845;
}

.care-layer p {
    color: #101D5F;
    font-weight: bold;
    font-family: "roboto slab";
}

@media screen and (max-width: 650px) {
    .care-slide {
        text-align: center;
        font-size: 18px;
        background: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .care-slide img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .mySwiperCare {
        width: 100% !important;
    }

    .care-row {
        padding: 8rem 0.5rem;
        background-color: #eee;
    }

    .care-col {
        position: relative;
        cursor: pointer;
    }

    .care-col:hover p {
        color: #E32845;
    }

    .care-layer {
        background-color: white;
        position: absolute;
        width: 90%;
        height: 30%;
        top: 80%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0rem 1rem;
    }

    .care-layer a {
        text-decoration: none;
        color: #101D5F;
        font-size: 15px;
    }

    .care-layer a:hover {
        color: #E32845;
    }

    .care-layer p {
        color: #101D5F;
        font-weight: bold;
        font-family: "roboto slab";
    }
}