.news-section {
    background-color: #eee;
    padding: 6rem 0rem;
}

.news-row {
    display: flex;
    justify-content: center;
    margin: 0rem 6rem;
    column-gap: 1rem;
}

.news-col.left {
    flex-basis: 60%;
}

.news-col.right {
    flex-basis: 40%;
}

.nl-img {
    position: relative;
    height: 10rem;
    overflow: hidden;
}

.nl-img img {
    height: 100%;
    transition: 1s;
}

.nl-col {
    display: flex;
    margin: 10px;
    background-color: white;
    padding: 10px;
    overflow: hidden;
}

.nl-flex {
    display: flex;
    justify-content: space-between;
}

.nl-flex p {
    color: #E32845;
}

.nl-content {
    padding: 0rem 1rem;
}

.nl-content h5 {

    font-size: 18px;
    font-family: "roboto slab";
    color: #101D5F;
    font-weight: bold;
    line-height: 30px;
    margin-bottom: 2rem;
}

.nl-content a {
    text-decoration: none;
    color: #101D5F;
}

.nr-img {
    position: relative;
    height: 10rem;
    width: 100%;
    overflow: hidden;
}

.nr-img img {
    height: 100%;
    width: 100%;
    transition: 1s;
}

.nr-col:hover .nr-img img,
.nl-col:hover .nl-img img {
    transform: scale(1.1);
}

.nr-row {
    height: 100%;
}

.nr-col {
    background-color: white;
    height: 94%;
    margin-top: 0.6rem;
    overflow: hidden;
}

.nr-flex {
    display: flex;
    justify-content: space-between;
}

.nr-flex p {
    color: #E32845;
}

.nr-content {
    padding: 0rem 1rem;
}

.nr-content a {
    text-decoration: none;
    color: #101D5F;
}

.nr-content h5 {
    font-size: 18px;
    font-family: "roboto slab";
    color: #101D5F;
    font-weight: bold;
    line-height: 30px;
    margin-bottom: 4rem;
}



