.courses-row {
    padding: 6rem 8rem;
    display: flex;
    justify-content: center;
    column-gap: 1.5rem;
}

.courses-left {
    flex-basis: 70%;
}

.courses-right {
    flex-basis: 35%;
}

.courses-img {
    position: relative;
    height: 27rem;
    width: 100%;
}

.courses-img img {
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.courses-rev-img {
    height: 7rem;
}

.courses-rev-img img {
    height: 100%;
}

.courses-left h2 {
    color: #111D5E;
    font-family: 'roboto slab';
    padding-bottom: 1rem;
    font-weight: bold;
}

.courses-flex1 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 6rem;
}

.courses-flex-img {
    position: relative;
    height: 3rem;
    display: flex;
    align-items: center;
}

.courses-flex-img img {
    height: 100%;
}

.courses-flex-img p {
    padding-top: 1rem;
    padding-left: 1rem;
}

.courses-flex1 span {
    color: #E32845;
}

.courses-flex2 {
    display: flex;
    column-gap: 2rem;
    height: 4rem;
}

.courses-flex2 p {
    padding: 7px 15px;
    cursor: pointer;
}

.courses-flex2 .active {
    background-color: #E32845;
    color: white;
}

.courses-content .high-title {
    color: #111D5E;
    font-family: 'roboto slab';
    font-size: 25px;
}

.courses-content {
    line-height: 35px;
}

.courses-details h3 {
    font-size: 21px;
    color: #111D5E;
    font-weight: bold;
}

.comments-flex {
    display: flex;
    align-items: center;
    line-height: 20px;
    position: relative;
}

.comments-flex h4 {
    color: #111D5E;
    font-size: 20px;
    font-weight: bold;
}

.comments-flex p {
    line-height: 25px;
    font-size: 15px;
}

.comments-img {
    position: relative;
    height: 5rem;
    flex-basis: 30%;
}

.comments-img img {
    height: 100%;
}

.comments-flex .btn {
    position: absolute;
    right: 0;
    top: 0;
}

.comments-form .card {
    border: none;
    padding: 3rem 0rem;
}

.comments-form .card-header {
    border: none;
    background-color: transparent;
}

.check-flex {
    display: flex;
    align-items: center;
}

.check-flex p {
    padding-top: 1rem;
    padding-left: 0.5rem;
}

.form-control:focus {
    border-color: #E32845;
    box-shadow: inset 0 1px 1px rgb(227, 40, 69, 0.2), 0 0 10px rgb(227, 40, 69, 0.2);
}

.courses-right .card {
    border: 0;
    background-color: #eee;
    margin-bottom: 1rem;
}

.courses-right .card-header {
    border: 0;
    background-color: transparent;
}

.courses-right .card-body {
    background-color: transparent;
}

.courses-rev {
    background-color: #eee;
    padding: 15px;
}

.courses-rev-img {
    position: relative;
    height: 15rem;
    width: 100%;
}

.courses-rev-img img {
    height: 100%;
    object-fit: contain;
    width: 100%;
}

.courses-rev-flex {
    display: flex;
    justify-content: space-between;
    margin: 10px 0px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.107);
}

.courses-rev button {
    background-color: #E32845;
    padding: 10px;
    color: white;
    border-radius: 0 !important;
    border: none;
}

.downloads {
    background-color: #eee;
    padding: 10px;
}

.downloads-flex {
    background-color: white;
    padding: 10px;
    margin: 10px 0px;
}

.downloads-flex p {
    display: flex;
    align-items: center;
}

.downloads-flex i {
    margin-right: 1rem;
    color: #E32845;
    font-size: 35px;
}

