.care-flex {
    display: flex;
    column-gap: 1rem;
}

.care-flex-img {
    position: relative;
    height: 20rem;
}

.care-flex-img img {
    height: 100%;
}

.care-flex-col {
    flex-basis: 50%;
}

.care-flex-col .fst-para {
    line-height: 24px;
    font-size: 15px;
}

.care-flex-col li {
    font-size: 15.5px;
    line-height: 18px;
    position: relative;
    list-style: none;
    margin: 15px 0px;
}

.care-flex-col li::before {
    content: "\2022";
    color: #E32845;
    width: 100%;
    height: 100%;
    position: relative;
    top: 0;
    left: 0;
    margin-right: 1rem;
    font-size: 23px;
}

.care-flex-img p {
    background-color: #E32845;
    color: white;
    height: 4.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
}

.courses-trand-flex-img {
    position: relative;
    height: 5rem;
}

.courses-trand-flex-img img {
    height: 100%;
}

.courses-trand-flex {
    display: flex;
    align-items: center;
    column-gap: 1rem;
    margin: 1rem 0rem;
    border-bottom: 1px solid rgb(0, 0, 0,0.1);
    padding-bottom: 1rem;
}