.center-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;

    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
}

.center-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.mySwiperCenter {
    width: 84% !important;
}

.center-col {
    position: relative;
}

.center-layer {
    position: absolute;
    width: 80%;
    height: 0%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
    transition: 0.5s;
}

.center-layer p:first-child {
    color: #E32845;
}

.center-layer p:last-child {
    color: #101D5F;
}

.swiper-slide-active .center-layer {
    height: 70%;
}

@media screen and (max-width: 600px) {

    .center-life-row
    {
        padding: 0rem 0.5rem;
    }
    .mySwiperCenter {
        width: 100% !important;
    }

