.subhero-col {
    position: relative;
}

.subhero-img {
    width: 100%;
    position: relative;
}

.subhero-img img {
    width: 100%;
}

.subhero-layer {
    background-color: rgb(17, 29, 94, 0.9);
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    color: white;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.subhero-links {
    display: flex;
}

.sub-link {
    text-decoration: none;
    color: white;
    margin-right: 0.5rem;
}

.subhero-links p {
    margin-left: 0.5rem;
    color: #E32845;
}

@media screen and (max-width: 1000px) {
    .subhero-col {
        position: relative;
    }

    .subhero-img {
        width: 100%;
        position: relative;
    }

    .subhero-img img {
        width: 100%;
    }

    .subhero-layer {
        background-color: rgb(17, 29, 94, 0.9);
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
        color: white;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
    }

    .subhero-links {
        display: flex;
    }

    .sub-link {
        text-decoration: none;
        color: white;
        margin-right: 0.5rem;
    }

    .subhero-links p {
        margin-left: 0.5rem;
        color: #E32845;
    }
}