.footer-row {
    display: flex;
    background-color: #111D5E;
    height: 30rem;
    align-items: center;
    justify-content: center;
    column-gap: 2rem;
    padding: 0rem 6rem;
}

.footer-col {
    flex-basis: 24%;
}

.footer-col:first-child {
    flex-basis: 35%;
    line-height: 35px;
}

.footer-img {
    position: relative;
    height: 5rem;
}

.footer-img img {
    height: 100%;
}

.footer-col {
    display: flex;
    flex-direction: column;
    color: white;
}

.footer-col b {
    color: white;
    margin-bottom: 1.5rem;
    font-size: 20px;
    font-family: 'roboto slab';
}

.footer-col a {
    text-decoration: none;
    color: white;
    margin: 6px 0px;
    transition: 0.5s;
}

.footer-col a:hover {
    color: #E32845;
}

.footer-down {
    background-color: #111D5E;
    color: white;
    height: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
}


@media screen and (max-width: 1000px) {
    .footer-row {
        display: flex;
        background-color: #111D5E;
        height: 35rem;
        align-items: center;
        justify-content: center;
        column-gap: 2rem;
        padding: 1rem 6rem;
        display: grid;
