@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400&family=Poppins:wght@200;400;600&family=Roboto:ital,wght@0,400;1,300&display=swap');


body {
    font-family: "poppins";
    margin: 0;
    padding: 0;
}

.top-navbar-row {
    display: flex;
    align-items: center;
    justify-content: space-around;
    background-color: #e32845;
    color: white;
    height: 3.4rem;
    white-space: nowrap;
}

.top-navbar-row h4 {
    font-size: 15px;
    color: white !important;
}

.top-navlinks {
    display: flex;
}

.top-navlinks a {
    text-decoration: none;
    color: white;
    margin: 15px;
    font-size: 15px;
}

.sticky-navbar-row {
    display: flex;
    color: white;
    justify-content: space-between;
    padding: 0rem 7.5rem;
    align-items: center;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.1);
}

.sticky-navbar-row.active {
    position: fixed;
    z-index: 10;
    width: 100%;
    background-color: white;
    width: 100%;
    top: 0;
    transition: 0.5s;
    animation: navAnime 0.5s linear 1 forwards;
}

@keyframes navAnime {
    from {
        top: -10%;
    }
    to {
        top: 0;
    }
}

.sticky-navbar-row .logo {
    position: relative;
    height: 6.5rem;
}

.logo img {
    height: 100%;
    object-fit: contain;
}

.down-navlinks {
    display: flex;
    align-items: center;
}

.down-navlinks i {
    font-size: 12px;
}

.down-navlinks a {
    text-decoration: none;
    margin: 15px;
    color: #113369;
    font-size: 16.5px;
    font-weight: bold;
    white-space: nowrap;
}

.nav-icons {
    color: #e32845;
    font-size: 38px;
    margin-left: 1rem;
    cursor: pointer;
}

.nav-icons .bx-menu {
    display: none;
}


.sidebar-row {
    background-color: white;
    position: fixed;
    top: 0;
    width: 26rem;
    right: -40%;
    box-shadow: 0px 15px 16px 0px rgba(0, 0, 0, 0.2);
    padding: 10px 20px;
    line-height: 30px;
    overflow-y: scroll;
    height: 40rem;
    transition: 0.5s;
    z-index: 5;
}

.sidebar-row.show {
    right: 0;
}

.sidebar-img {
    position: relative;
    height: 5rem;
}

.sidebar-img img {
    height: 100%;
}

.sidebar-col1 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 2rem;
}

.sidebar-col1 i {
    background-color: #e32845;
    padding: 10px;
    color: white;
    cursor: pointer;
}

.sidebar-col2 a {
    text-decoration: none;
    color: white;
    background-color: #e32845;
    padding: 10px;
}

.sidebar-col2 h3 {
    font-size: 21px;
    color: #113369;
}

.sidebar-col2 p {
    font-size: 15px;
}

.sidebar-col3 {
    margin-top: 3rem;
}

.sidebar-col3 h2 {
    font-size: 21px;
    color: #113369;
}


.contact-flex {
    display: flex;
    padding: 15px 0px;
    height: 3rem;
}

.contact-flex i {
    margin-right: 1rem;
    margin-top: 0.3rem;
}

.contact-social i {
    background-color: #e32845;
    color: white;
    margin: 5px;
    border-radius: 50px;
    padding: 10px;
    transition: 0.5s;
    cursor: pointer;
}

.contact-social i:hover {
    transform: translateY(-8%);
}

.sidebar-col4 a {
    text-decoration: none;
    color: white;
    background-color: #e32845;
    padding: 10px;
}

@media screen and (max-width: 1000px) {

    body {
        font-family: "poppins";
        margin: 0;
        padding: 0;
    }

    .top-navbar-row {
        display: flex;
        align-items: center;
        justify-content: space-around;
        background-color: #e32845;
        color: white;
        height: 3.4rem;
    }

    .top-navbar-row h4 {
        font-size: 12px;
    }

    .top-navlinks a {
        text-decoration: none;
        color: white;
        margin: 15px;
        font-size: 12px;
    }

    .sticky-navbar-row {
        display: flex;
        color: white;
        justify-content: space-between;
        padding: 0rem 7rem;
        align-items: center;
    }

    .sticky-navbar-row .logo {
        position: relative;
        height: 6rem;
    }

    .logo img {
        height: 100%;
        object-fit: contain;
    }

    .down-navlinks {
        display: flex;
        align-items: start;
        position: absolute;
        background-color: white;
        top: 22.5%;
        flex-direction: column;
        right: 12.5%;
        width: 75%;
        height: 0;
        overflow: hidden;
        transition: 0.5s;
        box-shadow: 0px 10px 16px 0px rgba(0, 0, 0, 0.2);
        z-index: 5;
    }

    .down-navlinks.active {
        height: 21rem;
    }

    .down-navlinks i {
        font-size: 12px;
    }

    .down-navlinks a {
        text-decoration: none;
        margin: 15px;
        color: #113369;
        font-size: 16.5px;
        font-weight: bold;
    }

    .down-navlinks .bx-menu-alt-right {
        color: red;
        font-size: 30px;
        margin-left: 1rem;
    }

    .nav-icons {
        color: #e32845;
        font-size: 45px;
        margin-left: 1rem;
        cursor: pointer;
    }

    .nav-icons .bx-menu {
        display: inline;
    }

    .sidebar-row {
        right: -65%;
    }
}

