.banner-row {
    height: 25rem;
    background-image: url(../images//banner.jpg);
    background-position: center;
    background-size: cover;
    position: relative;
    margin-top: 10rem;
}

.banner-layer {
    background-color: rgb(16, 29, 95, 0.8);
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.b-layer-col {
    flex-basis: 45%;
    line-height: 30px;
}

.b-layer-col h2 {
    font-weight: bold;
    font-size: 40px;
}

.b-layer-col a {
    text-decoration: none;
    color: white;
    background-color: #E32845;
    padding: 15px;
}

.b-layer-col p {
    margin-bottom: 2rem;
}

.b-layer-col:last-child {
    display: flex;
    align-items: center;
    justify-content: center;
}

.play-btn {
    font-size: 50px;
    background-color: white;
    color: #E32845;
    border-radius: 50%;
    padding: 10px;
    position: relative;
    z-index: 1;
    cursor: pointer;
}

.play-layer {
    background-color: white;
    width: 100%;
    height: 100%;
    position: absolute;
    border-radius: 50%;
    top: 0;
    z-index: -1;
    left: 0;
    transform: scale(1);
    transition: 0.5s;
    animation: scaleBtn 2s linear infinite forwards;
}

@keyframes scaleBtn {
    0% {
        transform: scale(0.8);
        opacity: 1;
    }

    60% {
        transform: scale(1.1);
        opacity: 0.5;
    }

    100% {
        transform: scale(1.6);
        opacity: 0;
    }
}

.b-layer-col:last-child p {
    margin-top: 2.5rem;
    margin-right: 2rem;
}


