.podcast-section {
    background-color: #eee;
    padding: 8rem 0rem;
}

.podcast-grids {
    display: flex;
    justify-content: center;
    padding: 0rem 7rem;
}

.podcast-grid-col {
    flex-basis: 40%;
    margin: 10px;
}

.podcast-grid-col:first-child {
    flex-basis: 80%;
}

.podcast-row {
    display: flex;
    flex-wrap: wrap;
}

.podcast-col {
    flex-basis: 50%;
    animation: animate 0.5s linear 1 forwards;
}

@keyframes animate {
    from {
        transform: scale(0);
    }

    to {
        transform: scale(1);
    }
}

.podcast-img {
    position: relative;
    height: 13rem;
}

.podcast-img img {
    height: 100%;
}

.podcast-col h3 {
    font-size: 20px;
    color: #101D5F;
    font-family: 'roboto slab';
}

.podcast-col p {
    color: #E32845;
}

.podcast-layer {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
}

.podcast-layer .pod-epi {
    background-color: white;
    width: 10rem;
    border-radius: 5px;
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 10%;
    left: 5%;
}

.pod-play {
    background-color: white;
    width: 3rem;
    position: absolute;
    bottom: 10%;
    left: 5%;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    height: 3rem;
    align-items: center;
    font-size: 30px;
    z-index: 1;
}

.pod-play-layer {
    position: absolute;
    background-color: white;
    width: 100%;
    height: 100%;
    top: 0;
    border-radius: 50%;
    z-index: -1;
    left: 0;
    transform: scale(1);
    animation: scaleBtn 2s linear infinite forwards;
}

@keyframes scaleBtn {
    0% {
        transform: scale(0.8);
        opacity: 1;
    }

    60% {
        transform: scale(1.1);
        opacity: 0.5;
    }

    100% {
        transform: scale(1.6);
        opacity: 0;
    }
}


.card-body {
    display: flex;
    flex-direction: column;
    background-color: #eee;
    line-height: 40px;
}

.card-header h3 {
    color: #101D5F;
    font-family: 'roboto slab';
}

.card-body p {
    text-decoration: none;
    color: black;
    cursor: pointer;
}

.form-body {
    background-image: url(../images//form-bg.jpg);
    height: 25rem;
    background-size: cover;
    background-position: center;
    z-index: 1;
    text-align: center;
}

.form-body i {
    font-size: 80px;
    text-align: center;
    color: white;
}

.form-body h3 {
    color: white;
    font-family: 'roboto slab';
    width: 20rem;
}

.form-layer {
    background-color: rgb(16, 29, 95, 0.6);
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
}

.form-body p {
    color: white;
}

.form-header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
}

