@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400&family=Poppins:wght@200;400;600&family=Roboto+Slab:wght@300;400;500&family=Roboto:ital,wght@0,400;1,300&display=swap');


.about-row {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10rem 7rem;
    box-shadow: 0px 15px 16px 0px rgba(0, 0, 0, 0.1);
    padding: 20px 0px;
    column-gap: 1.5rem;
}

.about-col {
    flex-basis: 50%;
}

.about-img {
    position: relative;
    width: 100%;
}

.about-img img {
    width: 100%;
}

.about-col h3 {
    color: #101D5F;
    font-size: 40px;
    font-family: "roboto slab";
    margin-bottom: 1.5rem;
}

.about-offers {
    display: flex;
    justify-content: space-around;
}

.about-offers-col {
    flex-basis: 50%;
}

.about-counter {
    display: flex;
}

.about-counter .count {
    margin: 30px;
    display: flex;
    flex-direction: column;
}

.count-num {
    display: flex;
}

.count p {
    font-size: 45px;
    color: #E32845;
}

.count h2 {
    font-size: 45px;
    transition: 10s;
}

.count-text p {
    font-size: 16px;
    color: rgba(0, 0, 0, 0.848);
}

.about-row a {
    text-decoration: none;
    color: white;
    background-color: #E32845;
    padding: 15px;
    cursor: pointer;
}



