.heading-container {
    text-align: center;
    height: 10rem;
    display: flex;
    justify-content: center;
}

.heading-row {
    width: 27rem;
}

.heading-row p {
    white-space: pre-wrap;
}

.heading-row h2 {
    font-size: 40px;
    font-family: "poppins sable";
    color: #101D5F;
    font-weight: bold;
}

@media screen and (max-width: 1000px) {
    .heading-container {
        text-align: center;
        height: 10rem;
        display: flex;
        justify-content: center;
    }

    .heading-row {
        width: 27rem;
    }

    .heading-row p {
        white-space: pre-wrap;
        font-size: 15px;
    }

    .heading-row h2 {
        font-size: 33px;
        font-family: "poppins sable";
        color: #101D5F;
        font-weight: bold;
    }
}


@media screen and (max-width: 650px) {
    .heading-container {
        text-align: center;
        height: 10rem;
        display: flex;
        justify-content: center;
    }

    .heading-row {
        width: 27rem;
    }

    .heading-row p {
        white-space: pre-wrap;
        font-size: 15px;
    }

    .heading-row h2 {
        font-size: 28px;
        font-family: "poppins sable";
        color: #101D5F;
        font-weight: bold;
    }
}