.swiper {
    width: 100%;
    height: 100%;
}

.story-row {
    padding: 5rem 0rem;
}

.story-slide {
    text-align: center;
    font-size: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 15px 16px 0px rgba(0, 0, 0, 0.3);
}

.mySwiperStories {
    width: 84%;
}

.story-col {
    height: 21rem;
    background-color: white;
    padding-top: 1rem;
}

.story-col h5 {
    color: #101D5F;
    font-family: 'roboto slab';
    margin-bottom: 2rem;
}

@media screen and (max-width: 650px) {
    .swiper {
        width: 100%;
        height: 100%;
    }

    .story-row {
        padding: 5rem 0rem;
    }

    .story-slide {
        text-align: center;
        font-size: 18px;
        display: flex;
        justify-content: center;
        align-items: center;
        box-shadow: 0px 15px 16px 0px rgba(0, 0, 0, 0.3);
    }

    .mySwiperStories {
        width: 100%;
    }

    .story-col {
        height: 21rem;
        background-color: white;
        padding-top: 1rem;
    }

    .story-col h5 {
        color: #101D5F;
        font-family: 'roboto slab';
        margin-bottom: 2rem;
    }
}