.center-p-row {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 6rem 5rem;
}

.center-p-col {
    flex-basis: 30%;
    margin: 10px;
    transition: 0.5s;
    box-shadow: 0px 10px 10px 0px rgba(0, 0, 0, 0.1);
}

.center-p-col:hover {
    transform: translateY(-4%);
}

.center-p-img {
    position: relative;
    height: 15rem;
    width: 100%;
}

.center-p-img img {
    height: 100%;
    width: 100%;
}

.center-p-col p {
    color: #E32845;
}

.center-p-col h3 {
    font-size: 20px;
    font-family: 'roboto slab';
    color: #111D5E;
}

.counter-container {
    padding: 6rem 5rem;
    background-color: #eee;
}

.counter-row {
    display: flex;
    justify-content: space-around;
    height: 15rem;
    align-items: center;
}

.count-flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

.count-flex h2 {
    color: #111D5E;
    font-size: 55px;
    font-weight: bold;
}

.count-flex p {
    color: #E32845;
    font-size: 55px;
}

.sl-row {
    display: flex;
    justify-content: center;
    margin: 0rem 7rem !important;
    place-content: center;
    place-items: center;
    column-gap: 1rem;
}

.sl-col-left {
    flex-basis: 50%;
}

.sl-col-left .sl-img {
    height: 27.5rem;
}

.sl-col-right {
    flex-basis: 50%;
}

.sl-col-right .sl-img {
    height: 13rem;
    margin: 13px 0px;
}

.sl-img {
    position: relative;
    height: 15rem;
    width: 100%;
}

.sl-img img {
    height: 100%;
}

.sl-col-right {
    display: flex;
    flex-direction: column;
}

.sl-img h3 {
    position: absolute;
    background-color: #E32845;
    color: white;
    bottom: 0;
    left: 2%;
    font-size: 19px;
    padding: 10px;
}

