.swiper {
    width: 100%;
    height: 100%;
}

.course-row {
    background-color: #eee;
    padding: 7rem 0rem;
}

.mySwiperCourse {
    width: 84% !important;
}

.course-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}

.course-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.course-flex {
    display: flex;
    justify-content: space-between;
    padding: 0rem 1rem;
}

.course-col i {
    color: #E32845;
}

.course-col h3 {
    font-size: 25px;
    text-align: start;
    color: #101D5F;
    font-family: "roboto slab";
}

.course-content .prof-info {
    display: flex;
    align-items: center;
}

.prof-info p {
    margin-top: 1rem;
    margin-left: 1rem;
    font-size: 16px;
}

.prof-img {
    height: 4rem;
    position: relative;
}

.prof-img img {
    height: 100%;
}

.teacher-name {
    color: #E32845;
}

.course-footer {
    display: flex;
    justify-content: space-between;
    padding: 0rem 1rem;
    margin-top: 2rem;
    color: #E32845;
    border-top: 1px solid rgba(0, 0, 0, 0.119);
}

.course-col {
    line-height: 50px;
}

.course-content {
    padding: 0rem 1rem;
}

